<!-- 서브 페이지 마크업 -->
<template lang="html">
  <div class="event_wrap">
    <div class="sub_tlt1">
      <h2>문화행사</h2>
    </div>
    <div class="intro">
      <span>문화행사</span>
      <p>꼭 가봐야 할 내 주변 문화행사를 확인해보세요!</p>
    </div>
    <div class="search">
      <label for="title">제목</label>
      <input id="title" type="text" v-model="value" />
      <button @click="search()">검색</button>
    </div>
    <div v-show="message" class="msg">{{ msgtext }}</div>

    <div>
      <v-data-table
        :headers="headers"
        :items="culture"
        :page.sync="page"
        :items-per-page="itemsPerPage"
        hide-default-footer
        class="elevation-1"
        @page-count="pageCount = $event"
      ></v-data-table>
      <div class="text-center pt-2">
        <v-pagination
          v-model="page"
          :length="pageCount"
          :total-visible="7"
        ></v-pagination>
      </div>
    </div>

    <!-- <img :src="x.main_img" alt="" class="rimg"> -->
  </div>
</template>

<script>
import jdata from "../SeoulCultureInfo.json";

export default {
  data() {
    return {
      message: false,
      msgtext: "검색 데이터가 없습니다",
      value: "",
      culture: jdata,
      page: 1,
      pageCount: 0,
      itemsPerPage: 10,
      headers: [
        {
          text: "분류",
          align: "start",
          value: "codename",
        },
        { text: "제목", value: "title" },
        { text: "기간", value: "date" },
        { text: "지역", value: "guname" },
        { text: "장소", value: "place" },
        // { text: "Image (g)", value: "<main_img>" },
      ],
    };
  },
  methods: {
    search: function () {
      let text = this.value;
      if (text) {
        this.culture = jdata.filter(function (element) {
          return element.title.includes(text); //조건에 참이면 객체를 배열로 리턴
        });

        if (this.culture.length == 0) {
          this.message = true;
          this.msgtext = "검색 데이터가 없습니다";
        } else {
          this.message = true;
          this.msgtext = this.culture.length + "개가 검색되었습니다";
        }
      } else {
        alert("검색어를 입력해주세요!");
      }
    },
  },
  
};
</script>

<style>
h2 {
  padding-top: 220px;
  font-size: 2.5em;
  font-weight: 300;
}
.sub_tlt1 {
  height: 500px;
  color: #fff;
  text-align: center;
  background: url(../assets/event_main.jpeg) no-repeat 50% 50%;
}
.intro {
  text-align: center;
  margin: 70px 0 50px;
}
.intro span {
  font-size: 3em;
  font-family: "Do Hyeon", sans-serif;
}
.intro p {
  font-size: 1.125em;
}

/* *******************search */
.search {
  width: 50%;
  text-align: center;
  border: 2px solid #0647cc;
  padding: 20px 0;
  border-radius: 5px;
  margin: 0 25% 70px 25%;
}
.search label {
  font-size: 1.125em;
  font-weight: 500;
  color: #0647cc;
}
.search input {
  border: 1px solid #adc8ff;
  margin: 0 20px 0 5px;
  padding: 5px;
  width: 50%;
}
.search button {
  font-weight: 500;
  padding: 5px 10px;
  border-radius: 5px;
  color: #fff;
  background: #0647cc;
}
.msg {
  text-align: center;
  color: #0647cc;
  font-size: 1.125em;
  font-weight: 500;
  margin-bottom: 20px;
}
/* *******************table */
.v-data-table {
  width: 80%;
  margin: 0 10% 50px 10%;
  border: 1px solid #adc8ff;
}
.text-start span {
  color: #0647cc;
}

.theme--light.v-pagination .v-pagination__item--active {
  color: #0647cc;
  font-weight: 700;
}
.v-pagination__navigation .v-icon {
  color: #0647cc;
}
.v-pagination.v-pagination {
  margin-bottom: 100px;
}

.theme--light.v-data-table
  > .v-data-table__wrapper
  > table
  > thead
  > tr:last-child
  > th
  span {
  font-size: 1.25em;
}


@media screen and (max-width: 1024px) {
  .v-data-table{
    width: 94%;
    margin: 0 3% 50px 3%;
  }
  .theme--light.v-data-table > .v-data-table__wrapper > table > thead > tr:last-child > th span{
    font-size: 1em;
  }
}
/* 모바일 */
@media screen and (max-width: 640px) {
  .v-data-table-header-mobile{
    display: none;
  }
  .v-data-table > .v-data-table__wrapper > table > tbody > tr > td{
    font-size: .75em;
  }
}
</style>
