<template>
    <!-- <div class="visual">
        <img src="../assets/back2.jpg" alt="">
    </div> -->

    <div id="visual">
        <div class="box">
            <swiper class="swiper" :options="swiperOption">
                <swiper-slide class="slide1"><p>#디지털감성 문화도시 서울</p></swiper-slide>
                <swiper-slide class="slide2"><p>문화와 낭만으로의 초대</p></swiper-slide>
                <swiper-slide class="slide3"><p>2천년을 이어온 서울의 문화</p></swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
                <div class="swiper-button-prev" slot="button-prev"></div>
                <div class="swiper-button-next" slot="button-next"></div>
            </swiper>
        </div>
    </div>


</template>

<style>

    :root {
    --swiper-theme-color: #0647cc;
}
    .box{
        height: 1000px; 
    }
    .swiper {
        height: 100%;
        width: 100%;
    }
    .swiper .swiper-slide{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .swiper .swiper-slide p{
      text-align: center;
      font-size: 4em;
      color: #fff;
      position: relative;
      font-family: 'Do Hyeon', sans-serif;
    }
    .swiper .swiper-slide p::before{
        content: '';
        width: 100%;
        height: 50px;
        background: rgba(6, 71, 204, .5);
        position: absolute;
        top: 30px;
        left: 0;
        z-index: -1;
    }
    .swiper .swiper-slide span{
        display: block;
    }
    .swiper .slide1{
        background: url(../assets/back.jpg) no-repeat 50% 50%;
    }
    .swiper .slide2{
        background: url(../assets/back2.jpg) no-repeat 50% 50%;
    }
    .swiper .slide3{
        background: url(../assets/back3.jpg) no-repeat 50% 50%;
    }
</style>

<script>
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'

export default {
    name: 'App',
    components: {
    Swiper,
    SwiperSlide
},
data() {
    return {
        swiperOption: {
        slidesPerView: 1,
        spaceBetween: 0,
        effect: 'fade',
        autoplay: {
            delay: 3000,
        },
        loop: true,
        pagination: {
            el: '.swiper-pagination',
            clickable: true
        },
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev'
        }
        }
    }
    }
}
</script>

