<!-- 서브 페이지 마크업 -->
<template lang="html">
  <div class="festival_warp">
    <div class="sub_tlt2">
      <h2>서울문화축제</h2>
    </div>
    <div class="intro">
      <span>서울문화축제</span>
      <p>서울의 특색있는 축제를 즐겨보세요!</p>
    </div>
    <div id="app">
      <v-app id="inspire">
        <v-card color="none" class="festival_inner">
          <v-tabs v-model="tab" background-color="#eaf1ff" color="#0647cc" grow>
            <v-tab v-for="item in items" :key="item">
              {{ item }}
            </v-tab>
          </v-tabs>

          <v-tabs-items v-model="tab">
            <v-tab-item
              v-for="(item, i) in items"
              :key="i"
              transition="fade-transition"
              reverse-transition="fade-transition"
            >
              <v-card color="basil" flat>
                <v-row class="fill-height" align="center" justify="center">
                  <v-carousel class="carousel" hide-delimiters>
                    <v-carousel-item
                      v-for="(src, key) in images[i]"
                      :key="key"
                      :src="src"
                    ></v-carousel-item>
                  </v-carousel>
                </v-row>
              </v-card>
              <v-simple-table>
                <template v-slot:default>
                  <tbody>
                    <tr v-for="(value, key) in info[i]" :key="key">
                      <td>{{ key }}</td>
                      <td>{{ value }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-tab-item>
          </v-tabs-items>
        </v-card>
      </v-app>
    </div>
  </div>
</template>

<script>
